.login-box {
  position: relative;
  z-index: 1;
  padding: 20px;
  border-radius: 10px;
  text-align: center;
  color: white;
  max-width: 400px;
  width: 100%;
  border: 2px solid red; /* Red border */
  background: rgba(0, 0, 0, 0.6); /* Semi-transparent background */
  box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.2);
  overflow: hidden;
}

.login-box::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: inherit; /* Inherit the background */
  backdrop-filter: blur(10px); /* Apply blur */
  z-index: -1;
  border-radius: 10px; /* To match the login box radius */
}
.login-head {
  font-size: 30px;
  text-align: left;
  margin: 20px;
}
.login-field {
  width: 80%;
  height: 35px;
  background-color: #2d2d2d;
  border: 0px;
  color: white;
  padding: 10px;
}
.login-text {
  font-size: 15px;
  margin: 20px;
}
.login-link {
  text-decoration: none;
}
.login-error {
  font-size: 15px;
}
.common-colour {
  color: white;
}

.movies-card {
  border: 3px solid red;
  width: 350px;
  border-radius: 5px;
  /* display: flex; */
}
.movie-img {
  border-radius: 5px;
  width: 100%;
  height: 350px;
}
.movie-title {
  font-size: 20px;
  margin: 20px;
}
.movie-overview {
  font-size: 15px;
  text-align: justify;
  margin: 20px;
}
.movie-flex {
  display: flex;
}
.movie-btn {
  margin: 20px;
}
.not-found {
  color: white;
  text-align: center;
  margin-top: 20px;
}
.search-btn {
  margin-top: 20px;
}
.nav-img {
  height: 50px;
  width: 200px;
  margin: 20px;
}
.movie-grid {
  display: grid;
  grid-template-columns: repeat(1, 1fr); /* 1 column on small screens */
  gap: 20px; /* Gap between the movie cards */
  padding: 10px;
  margin: 150px;
  margin-top: 100px;
}

.movies-card {
  background-color: black;
  padding: 10px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(247, 242, 242, 0.2);
  /* margin: 60px; */
}

.movie-img {
  width: 100%;
  border-radius: 10px;
}

.movies-card {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  padding: 8px;
  box-sizing: border-box;
  background-color: #000;
  border: 1px solid red;
}

.movie-content {
  flex-grow: 1;
  margin-bottom: 20px;
}

.movie-img {
  width: 100%;
  height: auto;
  margin-bottom: 10px;
}

.movie-btn {
  align-self: center;
  margin-top: auto;
}
.mov-nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

@media (min-width: 768px) {
  .movie-grid {
    grid-template-columns: repeat(2, 1fr); /* 2 columns on medium screens */
    /* margin: 0px;
    margin-top: 0px; */
  }
}

@media (min-width: 1200px) {
  .movie-grid {
    grid-template-columns: repeat(3, 1fr); /* 3 columns on large screens */
    /* margin: 0px;
    margin-top: 0px; */
  }
}
@media (max-width: 550px) {
  .movie-grid {
    /* padding: 54px; */
    margin-top: -10px;
    margin: 27px;
  }
  .nav-img {
    height: 40px;
    width: 120px;
    margin: 15px;
    margin-left: 0px;
  }
  .movies-card {
    height: 75%;
    padding: 6px;
    width: 304px;
  }
  .movie-img {
    height: 40%;
  }
  .movies-margine {
    margin-bottom: -190px;
    margin-left: -8px;
  }
  .login-field {
    margin-bottom: 15px;
  }
  .hero {
    margin: 30px;
  }
  .mov-btn {
    width: 86px;
  }
  .mov-nav {
    margin-top: 20px;
    align-items: center;
    justify-content: space-around;
  }
  .login-hero {
    margin-top: -160px;
  }
}
